      trendsChartData,
      pieChartOptions,
      doughnutChartOptions,
      lineChartOptions,
      getTeamProgressPercentage,
      getTeamProgressColor,
      getRiskScoreColor,
      getRiskLevelText,
      getRiskLevelClass,
      getRiskDescription,
      getProgressColor,
      viewAllCritical,
      formatDate,
      getSeverityType,
      getSeverityText,
      getStatusType,
      getStatusText
    };
  }
};
